.workflow__main-content {
  margin-bottom: 5.875rem;
}
.workflow__sub-content > img {
  height: 2.5rem;
  margin-bottom: 3rem;
}
.workflow__sub-content > h3 {
  margin-bottom: 1.6875rem;
}
@media (max-width: 71.8125em) {
  .workflow__sub-content:not(:last-child) {
    margin-bottom: 3.75rem;
  }
}
@media (min-width: 71.875em) {
  .workflow__flex {
    display: flex;
    flex-direction: row;
    gap: 2.1875rem;
  }
  .workflow__flex-item {
    flex: 1;
  }
}